/* Hero Section */
.heroSection {
  position: relative;
  text-align: center;
  z-index: 2; /* Đảm bảo rằng nó nằm trên các phần khác */
  margin-top: -100px; /* Di chuyển banner lên trên, phủ lên navbar */
}

/* Banner */
.heroBanner {
  width: 100%;
  max-width: 1200px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin-top: 20px;
  margin: 0 auto; /* Giữ banner cách một khoảng nhỏ so với nav */
}

/* Booking Section */
.bookingSection {
  padding: 30px 0;
  margin-top: -50px; /* Kéo phần booking lên gần banner */
  z-index: 2;
  position: relative;
}

.bookingForm {
  max-width: 1000px;
  margin: 0 auto;
  background-color: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border: 1px solid #ff7a00; /* Thêm viền giống như mẫu */
}

.tripType {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
  gap: 20px; /* Tạo khoảng cách giữa các lựa chọn */
  font-weight: bold;
}

.formGroup {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
}

.formGroup label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.formGroup input,
.formGroup select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.btnSearch {
  display: block;
  width: 100%;
  padding: 15px;
  background-color: #ff7a00;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
}

.btnSearch:hover {
  background-color: #e66a00;
}
